@import '../../sass/base/BaseStyles.scss';

.formContainer {
  padding-top: 40px;
  @include for-size(tablet) {
    padding-top: 160px;
  }
}

.title {
  @include font(700, 14px, 17px, $primary-font, $primary-letter-spacing);
  color: $accent-color;
  text-transform: uppercase;
  margin-bottom: 20px;
  text-align: center;

  @include for-size(tablet) {
    text-align: left;
  }
}

.input {
  padding: 40px 0 20px 0;
  width: 100%;
  color: $secondary-text-color;
  border: none;
  border-bottom: 1px solid $light-gray;
  outline: none;
  text-align: left;
  background-color: $primary-bg-color;
  @include font(700, 14px, 17px, $primary-font, $primary-letter-spacing);

  @include for-size(tablet) {
    width: 240px;
  }

  &::placeholder {
    color: $secondary-text-color;
  }
}

.wrapper {
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include for-size(tablet) {
    flex-direction: row;
    align-items: baseline;
  }
}

.button {
  @include orangeBtn;
  text-align: center;

  &:not(:last-child) {
    margin-bottom: 20px;

    @include for-size(tablet) {
      &:not(:last-child) {
        margin-right: 32px;
      }
    }
  }
}

.error {
  @include font(700, 14px, 17px, $secondary-font, $primary-letter-spacing);
  color: $accent-color;
}
